import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { DevelopmentRoutingModule } from './development-routing.module';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { WidgetsComponentsModule } from 'src/app/components/widgets/widgets-components.module';
import { NzLayoutModule } from 'ng-zorro-antd';
import { LayoutModule } from 'src/app/components/layout/layout.module';
import { DevelopmentComponent } from './development.component';


@NgModule({
    declarations: [DevelopmentComponent],
    imports: [
        CommonModule,
        DevelopmentRoutingModule,

        PerfectScrollbarModule,
        WidgetsComponentsModule,
        NzLayoutModule,
        LayoutModule,
    ]
})
export class DevelopmentModule { }
