import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DevelopmentComponent } from './development.component';


const routes: Routes = [
    {
        path: '',
        component: DevelopmentComponent,
        children: [
            {
                path: '',
                redirectTo: 'training'
            },
            {
                path: 'training',
                loadChildren: () => import('./training/training.module').then(mo => mo.TrainingModule)
            },

            {
                path: 'lisensi',
                loadChildren: () => import('./lisensi/lisensi.module').then(mo => mo.LisensiModule)
            },
            {
                path: 'proyek',
                loadChildren: () => import('./proyek/proyek.module').then(mo => mo.ProyekModule)
            },
        ]
    }

];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DevelopmentRoutingModule { }
